const container = document.getElementById('root');
const ajax = new XMLHttpRequest(); //ÏÉÅÏàò
const NEWS_URL = 'https://api.hnpwa.com/v0/news/1.json'
const CONTENT_URL = "https://api.hnpwa.com/v0/item/@id.json"

function getData(url){

    ajax.open('GET', url, false); // ÎèôÍ∏∞Ï†ÅÏúºÎ°ú Ï≤òÎ¶¨ÌïòÍ≤†Îã§.
    ajax.send();

    return JSON.parse(ajax.response);
}
function newsDetail(){
    // Î∏åÎùºÏö∞Ï†ÄÍ∞Ä Ï†úÍ≥µÌïòÎäî location 
    const id = location.hash.substring(1);

    const newsContent = getData(CONTENT_URL.replace('@id', id));
    const title = document.createElement('h1');

    container.innerHTML = `
    <h1>${newsContent.title}</h1>
    <div><a href="">Î™©Î°ùÏúºÎ°ú</a></div>
    `;
}
function newsFeed() {
    // JSON -> Object  
    const newsFeed = getData(NEWS_URL);
    const newsList = [];

    newsList.push('<ul>');
    for (let i = 0; i < 10; i++) {
        const div = document.createElement('div');
    
        const li = document.createElement('li');
        const a = document.createElement('a');
        
        newsList.push(
        `
        <li><a href="#${newsFeed[i].id}">${newsFeed[i].title} (${newsFeed[i].comments_count })</a></li>
        `
        );
        // ul ÏûêÏãùÎì§ÏùÑ Ìè¨Ìï®ÌïòÎäî Î∞©Î≤ï
        //ul.appendChild(div.children[0]);
        //ul.appendChild(div.firstElementChild);
    }
    newsList.push('</ul>');
    // Î∞∞Ïó¥Ïóê ÏûàÎäî ÏöîÏÜåÎ•º Î¨∏ÏûêÏó¥Î°ú Ìï©Ï≥êÏ§å.
    container.innerHTML = newsList.join('')
}

